<template>
  <div class="new-wrap">
    <!--    :class="{'short-screen': shortScreen}"-->
    <div class="menu-wrap">
      <!--<img src="./imgs/bg-set.png" alt="">-->
      <div class="head-img-box">
        <!--头像-->
        <img :src="userInfo.headImg" alt class="head-img" />
        <!--昵称天数-->
        <div class="nick-name flex-column">
          <span class="one-txt-cut">{{userInfo.nickname}}</span>
          <span>{{`累计打卡${pinchinInfo.punchinTotal}天`}}</span>
        </div>
      </div>

      <div class="main-cont">
        <!-- 菜单部分 -->
        <div class="menu-box flex">
          <router-link tag="div" class="menu-item flex-column" to="/remind">
            <img class="img" src="./imgs/icon-tip.png" alt />
            <span>提醒打卡</span>
          </router-link>

          <router-link tag="div" class="menu-item flex-column" to="/supply-punchin">
            <img class="repair" src="./imgs/icon-repair.png" alt />
            <span>进行补签</span>
          </router-link>
          <router-link tag="div" class="menu-item flex-column" to="/history">
            <img class="history" src="./imgs/icon-history.png" alt />
            <span>打卡历史</span>
          </router-link>

          <router-link tag="div" class="menu-item flex-column" to="/statistics">
            <img class="set" src="./imgs/icon-tongji.png" alt />
            <span>打卡统计</span>
          </router-link>

          <router-link tag="div" class="menu-item flex-column" to="/punchin-progress">
            <img class="set" src="./imgs/icon-xinlu.png" alt />
            <span>心路历程</span>
          </router-link>
          
          <div class="menu-item flex-column" @click="clog('set')">
            <img class="set" src="./imgs/icon-setimg.png" alt />
            <span>设置图片</span>
          </div>
        </div>

        <div class="line"></div>

        <!-- 打卡图部分 -->
        <MorningClock
          class="daka-img-box"
          v-if="choosedDate.clocked"
          :pinchinInfo="choosedDate.clockData"
        />

        <!-- 未打卡时的提示部分 -->
        <ClockTip
          v-if="!choosedDate.clocked && choosedDate.today"
          @click="toPinchin"
          :targetDate="choosedDate"
        ></ClockTip>
      </div>
    </div>
  </div>
</template>

<script>
import MorningClock from "./components/MorningClock";
import ClockTip from "./components/ClockTip/ClockTip";
import { Toast } from "mint-ui";
import mixin from "./components/mixin";
import { clog } from "../../api/punchin";
export default {
  name: "menuu",
  components: { MorningClock, ClockTip },
  mixins: [mixin],
  data() {
    return {
      punchinData: {},
      shortScreen: false
    };
  },
  methods: {
    clog(val) {
      Toast({
        message: "程序正在开发中",
        position: "center",
        duration: 2000
      });
      if (val === "set") {
        clog("早安打卡", [
          "vjf-h5-log",
          "transTime",
          "openid",
          "好物好生活平",
          location.href.split("?")[0],
          "点击设置图片",
          "打卡首页设置图片"
        ]);
        _hmt.push(["_trackEvent", "click", "首页", "点击设置图片"]);
      } else {
        clog("早安打卡", [
          "vjf-h5-log",
          "transTime",
          "openid",
          "好物好生活平",
          location.href.split("?")[0],
          "点击进行补签",
          "打卡首页补签"
        ]);
        _hmt.push(["_trackEvent", "click", "首页", "点击进行补签"]);
      }
    }
  }
};
</script>

<style scoped lang="scss">
.new-wrap {
  background-color: #f6f6f6;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.menu-wrap {
  background-color: #f6f6f6;
  background-image: url("../../assets/imgs/home-bg-top.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  width: 100%;
  height: 1218px;
  &.short-screen {
    min-height: 1150px;
    .main-cont {
      // margin-top: 176px;
    }
  }
  .head-img-box {
    width: 100%;
    height: 170px;
    display: flex;
    align-items: center;
    .head-img {
      width: 102px;
      height: 102px;
      border-radius: 50%;
      box-shadow: 0px 14px 32px 0px rgba(117, 20, 16, 0.21);
      background: #fff;
      margin-left: 54px;
    }

    .nick-name {
      height: 106px;
      margin-left: 60px;
      align-items: flex-start;
      justify-content: space-around;
      span {
        width: 360px;
        &:nth-of-type(1) {
          font-size: 40px;
          color: #fff;
          font-weight: bold;
        }
        &:nth-of-type(2) {
          font-size: 28px;
          color: rgba(255, 255, 255, 0.8);
        }
      }
    }
  }
  .main-cont {
    // height: 1020px;
    width: 710px;
    background: #fff;
    margin: 0 auto 20px;
    padding-bottom: 12px;
    border-radius: 10px;
    position: relative;
    .menu-box {
      height: 236px;
      box-sizing: border-box;
      padding: 18px 0px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-content: space-between;
      .menu-item {
        height: 88px;
        width: 230px;
        justify-content: space-between;
        align-items: center;
        position: relative;
        img {
          height: 49px;
          width: 49px;
          &.repair {
            height: 48px;
            width: 41px;
          }
          &.history {
            height: 50px;
            width: 44px;
          }
        }
        span {
          font-size: 20px;
          color: #646464;
        }
      }
    }
    .line {
      height: 10px;
      background: #f6f6f6;
    }
    .daka-img-box {
      width: 628px;
      /*height: 640px;*/
      margin: 30px auto 36px;
      border-radius: 10px;
    }
  }
}
</style>
